import { _decorator, Component, Node, VerticalTextAlignment, Label } from 'cc';
const { ccclass, property } = _decorator;
import { SudokuCreator } from '@algorithm.ts/sudoku';

@ccclass('GameController')
export class GameController extends Component {
    @property(Node)
    boardNode: Node;

    onLoad() {
        this.createPuzzleBoard();
    }

    update(deltaTime: number) {
        
    }
    createPuzzleBoard(){
        let sudokuCreator = new SudokuCreator({childMatrixWidth: 3});
        let boardData = sudokuCreator.createSudoku(0.2);
        let puzzleGame = boardData.puzzle;
        let solution = boardData.solution;
        let new_array: number[] = [];

        for (let i = 0; i < puzzleGame.length; i++) {
            new_array.push(puzzleGame[i] + 1);
        }

        console.log(puzzleGame);
        console.log(solution);

        for (let i = 0; i < 9; i++) {
            for (let j = 0; j < 9; j++) {
                const value = new_array[i*9 + j];
                const node = new Node();
                node.setPosition(i * 55, j * 55);
                this.boardNode.addChild(node);
                
                const labelBlock = node.addComponent(Label);
                labelBlock.string = value === -1 ? "-" : value.toString();

            }
        }

    }   
}
